import { Box, Button, InputAdornment, Paper, Tab, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField } from '@material-ui/core';
import { Close, Search } from '@material-ui/icons';
import React, { useEffect, useState } from 'react';
import { Api } from '../../functions';
import { Connect, mapStateToProps } from '../../redux';
import useStyles from './styles';

function Index(props) {
    const [state, setState] = useState({
        searchVal: '',
        withdrawals: []
    }),
        { searchVal, withdrawals } = state,
        { User } = props,
        classes = useStyles(),
        columns = [
            { id: '_id', label: 'User Id', minWidth: 100 },
            { id: 'accountinfo', label: 'Account Info', minWidth: 220 },
            { id: 'transactionId', label: 'Transaction Id', minWidth: 220 },
            { id: 'amount', label: ' Amount', minWidth: 200 },
            { id: 'currencyAmount', label: 'Order Currency', minWidth: 200 },
            { id: 'date', label: 'Date', minWidth: 200 },
            {
                id: 'action',
                label: 'Action',
                sublabel: 'text',
                minWidth: 120,
                align: 'center'
            }
        ],
        fetchWithdrawals = () => {
            Api({
                module: 'Orders',
                submodule:'support',
                data: {
                    action: '',
                    userId: User._id
                }
            }).then(withdrawals => setState({ ...state, withdrawals })).catch(e => console.log(e))
        }
    useEffect(() => {
        fetchWithdrawals()
        return () => {

        }
    }, [])
    return (
        <Paper>
            <Box>
                <TextField
                    placeholder={'search by order id'}
                    className={classes.searchInput}
                    value={searchVal}
                    classes={{ notchedOutline: classes.input }}
                    onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment position="start">
                                <Search style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </InputAdornment>
                        ),
                        endAdornment: (
                            <Button position="end" onClick={() => setState({ ...state, searchVal: '' })}>
                                <Close style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </Button>
                        ),
                        classes: { notchedOutline: classes.noBorder }
                    }}
                />
                <TableContainer className={classes.container}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                {columns.map(a => <TableCell
                                    key={a.id}
                                    align={a.align}
                                    style={{ minWidth: a.minWidth }}>
                                        {a.label}
                                </TableCell>)}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                        {withdrawals.filter(a => (searchVal && a._id === searchVal) || !searchVal).map(row => {
                           return ( <TableRow>
                               {columns.map((column) => {
                                   const value = row[column.id];
                               <TableCell>
                                   {value}
                                </TableCell>
                                 })}
                            </TableRow>)
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index);