import { connect } from 'react-redux';
import AsyncStorage from '@react-native-community/async-storage';
import thunk from 'redux-thunk';
import { persistStore, persistReducer } from 'redux-persist';
import { createStore, applyMiddleware } from 'redux';

const Connect = connect,
initialState = {
    User: '',
    isLoggedIn: false,
    notes:{
        Disputes:0,
        Chats:0
    },
    usersNumber:0
},
    reducer = (state = initialState, action) => {
        const { field, val, type } = action;
        if (type === 'setByField')
            return { ...state, [field]: val }
        else if (type === 'multiplefields')
            return { ...state, ...val }
        return type === 'logOut' ? initialState : state;
    },
    mapStateToProps = (state) => state,
    mapDispatchToProps = (dispatch) => {
        return {
            setByField: (field, val) => dispatch({ type: 'setByField', field, val }),
            setMultipleFields: (val) => dispatch({ type: 'multiplefields', val }),
            logOut: () => dispatch({ type: 'logOut' })
        };
    },
    persistConfig = {
        key: 'authType',
        storage: AsyncStorage
    },
    pReducer = persistReducer(persistConfig, reducer),
    middleware = applyMiddleware(thunk, /*logger*/),
    store = createStore(pReducer, middleware),
    persistor = persistStore(store);
export {
    Connect,
    mapStateToProps,
    mapDispatchToProps,
    persistor,
    store
}