import { Box, Button, InputAdornment, Modal, Paper, Tab, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField } from '@material-ui/core';
import { Close, Search } from '@material-ui/icons';
import React, { Fragment, useEffect, useState } from 'react';
import { Api, getDateTime } from '../../functions';
import { Connect, mapStateToProps } from '../../redux';
import useStyles from './styles';
import { useHistory } from 'react-router-dom';
import Prompt from '../common/Prompt';
import Notify from "../Notifications";

function Index(props) {
    const [state, setState] = useState({
        searchVal: null,
        orders: [],
        openPrompt: false,
        orderId: null,
        loading: false,
        options: {
            title: '',
            message: ''
        },
        propmpOption: null,
        openCompletionModal: false,
        seller: null,
        transactionId: null,
        error: null,
        completeLoading: false
    }),
        history = useHistory(),
        { searchVal, orders, openPrompt, orderId, options, loading, propmpOption, openCompletionModal, seller, transactionId, error, completeLoading } = state,
        { User } = props,
        classes = useStyles(),
        columns = [
            { id: 'seller', label: 'Seller', minWidth: 200 },
            { id: 'amount', label: ' Amount', minWidth: 100 },
            { id: 'currencyAmount', label: 'Order Currency', minWidth: 100 },
            { id: 'date', label: 'Date', minWidth: 200 },
            {
                id: '_id',
                label: 'Action',
                sublabel: 'text',
                minWidth: 200,
                align: 'center'
            }
        ],
        handleCompletionModal = () => {
            setState({ ...state, openCompletionModal: !openCompletionModal })
        },
        completeOrder = () => {
            if (transactionId) {
                let order = orders.find(a => a._id === orderId)
                setState({ ...state, completeLoading: true })
                Api({
                    module: 'Orders',
                    submodule: 'support',
                    data: {
                        action: 'updateOrderStatus',
                        userId: User._id,
                        status: "Completed",
                        orderId,
                        sellerId: order.seller._id,
                        amount: order.amount,
                        transactionId
                    }
                }).then(({ message, status }) => {
                    Notify(message, status);
                    fetchOrders();
                })
            } else
                setState({ ...state, error: 'Transaction ID is required' })

        },
        cancelOrder = (option) => {
            if (option === 'Yes')
                Api({
                    module: "Orders",
                    data: {
                        action: 'updateOrderCancellation',
                        orderId,
                    }
                }).then(({ message, status }) => {
                    Notify(message, status);
                    fetchOrders();
                }).catch(err => console.log(err))
            setState({ ...state, openPrompt: false })
        },
        fetchOrders = () => {
            Api({
                module: 'Orders',
                submodule:'support',
                data: {
                    action: 'fetchPendingWithdraws',
                    userId: User._id
                }
            }).then(({ orders }) => {
                setState({ ...state, orders, openPrompt: false, completeLoading: false });
            }).catch(e => console.log(e))
        },
        checkBalance = (orderId, sellerId) => {
            // alert(sellerId)
            setState({ ...state, loading: true, orderId });
            Api({
                module: "Orders",
                data: {
                    action: "checkWithdrawBalance",
                    sellerId
                }
            }).then(({ message, status }) => {
                setState({ ...state, loading: false, orderId: null });
                Notify(message, status);
            }).catch(err => alert(err))
        }
    useEffect(() => {
        let page = 'Dashboard'
        if (!User.Roles.includes('WithdrawAgent') || !User.Roles.includes('SuperAdmin'))
            history.push('/', { page })
        fetchOrders()
        return () => {

        }
    }, [])
    return (
        <Paper>
            <Box>
                <TextField
                    placeholder={'search by order id'}
                    className={classes.searchInput}
                    value={searchVal}
                    classes={{ notchedOutline: classes.input }}
                    onChange={(c) => setState({ ...state, searchVal: c.target.value })}
                    InputProps={{
                        disableUnderline: true,
                        startAdornment: (
                            <InputAdornment position="start">
                                <Search style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </InputAdornment>
                        ),
                        endAdornment: (
                            <Button position="end" onClick={() => setState({ ...state, searchVal: '' })}>
                                <Close style={{ color: '#8C8B8B', marginLeft: 10, marginRight: 10, fontSize: 16 }} />
                            </Button>
                        ),
                        classes: { notchedOutline: classes.noBorder }
                    }}
                />
                <TableContainer className={classes.container}>
                    <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                {columns.map(a => <TableCell
                                    key={a.id}
                                    align={a.align}
                                    style={{ minWidth: a.minWidth }}>
                                    {a.label}
                                </TableCell>)}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {orders.filter(a => (searchVal && a._id === searchVal) || !searchVal).map(row => {
                                return (<TableRow>
                                    {columns.map((column) => {
                                        const value = row[column.id];
                                        return (<TableCell>
                                            {column.id === 'seller' ? <Fragment>
                                                {
                                                    Object.keys(row[column.id]).filter(q => q !== '_id').map(key => (
                                                        <div className={classes.smallText0}><span style={{ fontWeight: "bold" }}>{key}</span>: {row[column.id][key]}</div>
                                                    ))
                                                }
                                            </Fragment> : column.id === 'currencyAmount' ? <Fragment>
                                                {
                                                    Object.keys(row[column.id]).filter(q => q !== '_id').map(key => (
                                                        <div className={classes.smallText0}> <span style={{ fontWeight: "bold" }}>{key}</span>: {row[column.id][key]}</div>
                                                    ))
                                                }
                                            </Fragment> : column.id === 'date' ? getDateTime(value).date : column.id === '_id' ? (!(loading && orderId === value) ? <div>
                                                <Button className={classes.btnCheckWithdraw} onClick={() => {
                                                    checkBalance(value, row['seller']._id)
                                                }}>Check Balance</Button>
                                                <Button className={classes.btnCancel} onClick={() => {
                                                    setState({
                                                        ...state,
                                                        openPrompt: true,
                                                        orderId: value,
                                                        options: { title: 'Order withdraw cancellation', message: 'Are you sure you want to cancel this order withdraw?' },
                                                        propmpOption: cancelOrder
                                                    })
                                                }}>Cancel</Button>
                                                <Button className={classes.btnComplete} onClick={() => {
                                                    setState({
                                                        ...state,
                                                        openCompletionModal: true,
                                                        orderId: value,
                                                        seller: orders.find(a => a._id === value).seller
                                                    })
                                                }}>
                                                    Complete
                                                </Button>
                                            </div> : <div>Please wait...</div>) : value}
                                        </TableCell>)
                                    })}
                                </TableRow>)
                            })}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
            {
                openPrompt ?
                    <Prompt
                        open={openPrompt}
                        propmpOption={propmpOption}
                        options={options}
                    /> :
                    null
            }
            {
                openCompletionModal ? <Modal
                    open={handleCompletionModal}
                    onClose={handleCompletionModal}
                    aria-labelledby="modal-modal-title"
                    aria-describedby="modal-modal-description"
                >
                    <Box>
                        <div className={classes.boxModal}>
                            <div className={classes.modalMainBox}>
                                <div className={classes.modalHeader}>Sellers Info</div>
                                <div className={classes.sellersInfo}>
                                    {
                                        Object.keys(seller).filter(q => q !== '_id').map(a => <div className={classes.smallText0}><span style={{ fontWeight: "bold" }}>{a}</span>: {seller[a]}</div>)
                                    }

                                </div>
                                <TextField variant="outlined" label="Transaction Id" onChange={(e) => setState({ ...state, transactionId: e.target.value })} error={error} helperText={error} onFocus={() => setState({ ...state, error: null })} />
                                {!completeLoading ? <Button onClick={completeOrder} className={classes.btnSaveModal}>Save</Button> : null}
                            </div>
                        </div>
                    </Box>
                </Modal> : null
            }
        </Paper>
    )
}

export default Connect(mapStateToProps)(Index);