import React, { useState } from 'react';
import Notify from "../Notifications";
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import useStyles from './styles'
import logo from '../../mages/logo.png'
import { Api } from '../../functions';
function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        benkiko.africa
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}
export default function SignUp() {
  const classes = useStyles(),
    [state, setState] = useState({
      roleSelect: []
    }),
    { roleSelect } = state,
    handleChange = (event) => {
      setState({ ...state, role: event.target.value })
    },
    roles = [
      { name: "Superadmin", value: "SuperAdmin" },
      { name: "Withdraw Agent", value: "WithdrawAgent" },
      { name: "Support", value: "support" },
      { name: "Accountant", value: "Accountant" }
    ],
    onSubmit = (e) => {
      e.preventDefault();
      let formData = {},
        data = new FormData(e.target);
      data.forEach((value, key) => {
        formData[key] = key === 'Roles' ? roleSelect.map(a => a.value) : value;
      });
      Api({
        module: 'Support',
        submodule:'support',
        data: {
          action: 'Register',
          ...formData
        }
      }).then(({ status, message }) => {
        Notify(message, status)
      }).catch(e => alert(e))
    }
  return (
    <Container component="main" maxWidth="xs" >
      <CssBaseline />
      <div className={classes.paper}>
        <img alt='' src={logo} className={classes.logo} />
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} method={'post'} onSubmit={onSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="FirstName"
                variant="outlined"
                required={true}
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="LastName"
                label="Last Name"
                name="LastName"
                autoComplete="lname"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="username"
                label="Username"
                name="Username"
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="Email"
                type={'email'}
                autoComplete="email"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="Password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
              />
            </Grid>
            <Grid item xs={12}>
              <Autocomplete
              
                multiple
                limitTags={2}
                onChange={(event, newValue) => {  
                  setState({ ...state, roleSelect: newValue })
                }}
                id="multiple-limit-tags"
                options={roles}
                getOptionLabel={(option) => option.name}
                renderInput={(params) => (
                  <TextField {...params} variant="outlined" name="Roles" fullWidth label="Roles" placeholder="Role" />
                )}
              />
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign Up
          </Button>
        </form>
      </div>
      <Box mt={5}>
        <Copyright />
      </Box>
    </Container>
  );
}
